export default {
	data() {
		return {
			isLoaing: true,
			orderid: null,
			orderDetail: null,
			modelName: '',
			modelTxt: '',
			showBtn: 0,
			shipping_address: null,
			shipping_phone: null
		}
	},
	onLoad(options) {
		this.orderid = options.orderid
		this.getOrderDetail()
		this.showBtn = options.aftersaleStatus
	},
	methods: {
		//查看自提信息
		showCodeMsg() {
			//获取自提地址
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/storeSelfAddress",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						let result = res.data.data
						this.shipping_address = result.province + result.city + result.area + result.detail
						this.shipping_phone = result.phone
					}
					this.modelName = 'codeMsg'
				}
			})
		},
		toReturnGoods() {
			uni.navigateTo({
				url: "../returnGoods/returnGoods"
			})
		},
		showModel(str) {
			this.modelTxt = str
			if (str == '删除订单') {
				this.showToast('该功能暂未开放')
				return
			}
			this.modelName = 'showModel'
		},
		getOrderDetail() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/orderdetail",
				data: {
					token: uni.getStorageSync('token'),
					id: this.orderid
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.isLoaing = false
					this.orderDetail = res.data.data
					this.orderDetail.add_time = this.handleTime(this.orderDetail.add_time)
					this.orderDetail.total_price = parseFloat(this.orderDetail.total_price).toFixed(2)
					uni.setStorageSync('orderDetail', this.orderDetail)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		//处理下单时间
		handleTime(time) {
			let date = new Date(time * 1000)
			let Y = date.getFullYear().toString()
			let M = (date.getMonth() + 1).toString().padStart(2, '0')
			let D = date.getDate().toString().padStart(2, '0')
			let h = date.getHours().toString().padStart(2, '0')
			let m = date.getMinutes().toString().padStart(2, '0')
			let s = date.getSeconds().toString().padStart(2, '0')
			let newTime = Y + '-' + M + '-' + D + ' ' + h + ':' + m + ':' + s
			return newTime
		},
		//跳转选择售后类型
		toAftersale() {
			uni.redirectTo({
				url: "../chooseAftersaleType/chooseAftersaleType?orderid="+this.orderid
			})
		},
		closeModel() {
			this.modelName = ''
		},
		//点击弹出框里的确认
		sure() {
			switch (this.modelTxt) {
				case '删除订单':
					this.sureDel()
					break;
				case '取消订单':
					this.cancleOrder()
					break;
				case '收货':
					this.sureGet()
					break;
				default:
					break;
			}
			this.closeModel()
		},
		//取消订单
		cancleOrder() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/orderCancel",
				data: {
					id: this.orderid,
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						uni.showToast({
							title: '订单已取消',
							mask: true,
							success: () => {
								setTimeout(function() {
									uni.navigateBack()
								}, 1000);
							}
						})
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//订单付款
		orderPay() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/Order/pay',
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.orderid
				},
				header: this.header,
				method: "POST",
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: res.data.data.timeStamp,
							nonceStr: res.data.data.nonceStr,
							package: res.data.data.package,
							signType: res.data.data.signType,
							paySign: res.data.data.paySign,
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
								uni.redirectTo({
									url: "../paySuccess/paySuccess?type=1"
								})
							},
							fail: function(err) {
								console.log('fail:' + JSON.stringify(err));
							}
						});
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//申请退款
		returnMoney() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/returnGoods",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.orderid,
					type: 0
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						uni.showToast({
							title: '已提交申请',
							mask: true,
							success: () => {
								setTimeout(function() {
									uni.navigateBack()
								}, 1000);
							}
						})
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//删除订单
		sureDel() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/orderDelete",
				data: {
					order_id: this.order_id,
					shop_id: uni.getStorageSync('shop_id'),
					token: uni.getStorageSync('token')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						uni.showToast({
							title: '订单已删除',
							icon: 'success',
							success: () => {
								setTimeout(function() {
									uni.navigateBack({
										delta: 1
									})
								}, 1500)
							}
						})
					}
				}
			})
		},
		//查看物流
		checkLogistics() {
			// this.showToast('该功能暂未开放')
			uni.navigateTo({
				url: '../expressInfo/expressInfo?orderid=' + this.orderid
			})
		},
		//提醒发货
		remindSend() {
			this.showToast('该功能暂未开放')
			return
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/remindSend",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.order_id,
					shop_id: uni.getStorageSync('user').shop_id
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						this.showToast('已提醒商家尽快发货')
					}
				}
			})
		},
		//确认收货
		sureGet() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/confirmGet",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.orderid
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						uni.showToast({
							title: '已确认收货',
							mask: true,
							success: () => {
								setTimeout(function() {
									uni.navigateBack()
								}, 1000);
							}
						})
						return
					}
					this.showToast(res.data.msg)
				}
			})
		}
	}
}
